import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import LoadingSpinner from '../../components/shared/loadingSpinner/LoadingSpinner';
import NotFound from '../../components/shared/notFound/NotFound';
import useFetch from '../../lib/hooks/useFetch';

import { Container } from '../popularUsers/PopularUsers.style';
import {
	Profile,
	ImgContainer,
	UserAvatar,
	UserName,
	DataBox,
	UserInfoContainer,
	InfoListContainer,
	InfoItem,
	RepoContainer,
	Repo,
} from './UserPage.style';

const UserPage = () => {
	const [user, setUser] = useState({});
	const [repos, setRepos] = useState([]);
	const { username } = useParams();
	const { loading, getUserWithRepos, error } = useFetch();

	useEffect(() => {
		const fetchUser = async () => {
			try {
				const { profile, repos } = await getUserWithRepos(username);

				setUser(profile);
				setRepos(repos);
			} catch (error) {
				console.log(error);
			}
		};
		fetchUser();
	}, [getUserWithRepos, username]);

	useEffect(() => {
		console.log(user);
	}, [user]);

	return (
		<div>
			{loading && <LoadingSpinner asOverlay />}
			{error && !loading && <NotFound />}
			{!error && !loading && (
				<Container>
					<Profile>
						<div>
							<ImgContainer>
								<UserAvatar src={user.avatar_url} />
							</ImgContainer>
							<UserName>{user.login}</UserName>
						</div>
						<UserInfoContainer>
							<DataBox color="00A8CC">Public Repos: 54</DataBox>
							<DataBox color="00CC83">Followers: 784000</DataBox>
							<InfoListContainer>
								<InfoItem>Company: Linux Foundation</InfoItem>
								<InfoItem>Location: Portland, OR</InfoItem>
								<InfoItem>Member Since: 2011-09-03T15:26:22Z</InfoItem>
								<InfoItem>Location: Portland, OR</InfoItem>
							</InfoListContainer>
						</UserInfoContainer>
					</Profile>
					<h1>Latest Repos</h1>
					<RepoContainer>
						<Repo>
							<h3>name</h3>
							<div>
								<DataBox>Stars: 604</DataBox>
								<DataBox>Watchers: 604</DataBox>
							</div>
						</Repo>
					</RepoContainer>
				</Container>
			)}
		</div>
	);
};

export default UserPage;
